import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
// import equip from "../views/Idle/equip";
import login from "../views/login"
import home from "../views/layout/home"
import register from "../views/register";
import error from "../views/error";
import vip from "../views/layout/vip"
import store from '../store/index'
import cookie from 'vue-cookies'
import Uequip from "../views/layout/Uequip";
Vue.use(VueRouter)
Vue.use(cookie)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
    {
      path: '/Uequip',
      name: 'Uequip',
      component: Uequip,

    },
    {
      path: '/register',
      name: 'register',
      component: register,

    },
    {
      path: '/vip',
      name: 'vip',
      component: vip,
      meta:{
        requireAuth: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/error',
      name: 'error',
      component: error
    },
    {
      path: '/home',
      name: 'home',
      component: home,
      meta:{
        requireAuth: true
      }
    },
  {
    path: '/about',
    name: 'About',

    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,

})
const whiteList = ['Home','login','register','equip','About'] //放行的路径
router.beforeEach((to, from, next) => {
    if (to.path === '/home' || to.path==='/vip' || to.path==='/Uequip'){
      if (store.state.token || cookie.get("id") ){
        next()
      }else {
        next({path:'/login'})
      }
    }else {
      //to。name 是下一个要去的路径名字   如果存在白名单中，则直接放行
      if (whiteList.indexOf(to.name) !== -1){
        next()
      }else {
        next({path:'/login'})
      }
    }
})

export default router
