        <el-container>
            <el-header style="font-size: 14px; " >
                <el-row :gutter="20">
                    <el-col :span="6"><div class="grid-content bg-purple">
                        <el-col :span="6"><div class="grid-content bg-purple">
                            <router-link to="/" style="color: azure"><el-button type="primary"  >主页</el-button></router-link>
                        </div>
                        </el-col>
                    </div>
                    </el-col>
                </el-row>
            </el-header>
            <el-container>
                <el-aside width="200px"></el-aside>
                <el-main>
                    <el-container>
                        <el-aside width="200px"></el-aside>
                        <el-form ref="regForm" v-bind:model="user" label-width="100px"  :rules="rules">
                            <el-form-item label="电话" prop="id" size="medium" >
                                <el-input v-model="user.id" placeholder="电话"></el-input>
                            </el-form-item>
                            <el-form-item label="邮箱" prop="email" size="medium">
                                <el-input v-model="user.email" placeholder="邮箱" type="email" ></el-input>
                            </el-form-item>
                            <el-form-item label="昵称" prop="name" size="medium">
                                <el-input v-model="user.name" placeholder="昵称" ></el-input>
                            </el-form-item>

                            <el-form-item label="密码" prop="password" size="medium">
                                <el-input v-model="user.password" placeholder="密码" type="password" show-password  ></el-input>
                            </el-form-item>
                            <el-form-item label="性别" prop="sex">
                                <el-radio-group v-model="user.sex">
                                    <el-radio label="男"></el-radio>
                                    <el-radio label="女"></el-radio>
                                </el-radio-group>
                            </el-form-item>
                            <br>
                            <el-form-item>
                                <el-button type="primary" @click="onSubmit" >注册</el-button>
                                <el-button @click="resetForm('regForm')">重置</el-button><br><br>
                            </el-form-item>
                        </el-form>


                    </el-container>

                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
    export default {
        name: "register",
        data(){
            return{
                user:{
                    id:'',
                    name:'',
                    password:'',
                    sex:'男',
                    email:'',
                },
                rules: {
                    id: [
                        { required: true, message: '请输入电话', trigger: 'blur' },
                        { min: 10, max: 20, message: '请输入正确的电话', trigger: 'blur' }
                    ],
                    name: [
                        { required: true, message: '请输入昵称', trigger: 'blur' },
                    ],
                    sex: [
                        { required: true, message: '请选择性别', trigger: 'blur' },
                    ],
                    email: [
                        { required: true, message: '请输入邮箱', trigger: 'blur' },
                        { min: 10, max: 20, message: '请输入正确的邮箱', trigger: 'blur' }
                    ],
                    password: [
                        { required: true, message: '请输入密码', trigger: 'blur' },
                        { min: 8, max: 20, message: '密码长度不能小于8位大于20位', trigger: 'blur' }
                    ],
                }
            }
        },
        methods:{
            onSubmit(){
                // eslint-disable-next-line no-unused-vars
                let _this = this

                this.$axios.post('http://47.95.237.152:8081/User/register/',this.$qs.stringify({
                    id:this.user.id,
                    name:this.user.name,
                    password:this.user.password,
                    sex:this.user.sex,
                    email:this.user.email,
                    })
                ).then(function (resp) {
                    if (resp.status === 200){
                        let data = eval(resp.data)
                        if (data.code === 200){
                            _this.store.state.token = _this.user.id
                            _this.$cookie.set("id",_this.id);
                            _this.$router.push({
                                name:'home',
                                params:{
                                    id:_this.user.id,
                                }

                            })
                        }else {
                            console.log("注册失败！")
                        }

                    }
                })},
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

