<template>
    <!--                    <form enctype="multipart/form-data" method="post" v-on:submit.prevent="onSubmit()" >-->
    <!--                        <label >电话：-->
    <!--                            <input type="text" v-model="user.id" placeholder="请输入你的电话或邮箱！">-->
    <!--                        </label><br>-->
    <!--                        <label> 名字：-->
    <!--                            <input type="text" v-model="user.name" placeholder="请输入你的名字！">-->
    <!--                        </label><br>-->
    <!--                        <label> 密码：-->
    <!--                            <input type="password" v-model="user.password" placeholder="请输入你的密码！">-->
    <!--                        </label><br>-->
    <!--                        <button  type="submit" >登录</button> <br>-->
    <!--                        <router-link to="register">注册点击这里</router-link>-->
    <!--                    </form>-->
    <div class="login">

        <el-container>
            <el-header style="font-size: 14px; " >
                <div style="text-align: left">
                    <el-row :gutter="20">
                        <el-col :span="6"><div class="grid-content bg-purple">
                            <el-col :span="6"><div class="grid-content bg-purple">
                                <router-link to="/" style="color: azure"><el-button type="primary"  >主页</el-button></router-link>
                            </div>
                            </el-col>
                        </div>
                        </el-col>
                    </el-row>
                </div>
            </el-header>
            <el-container>
                <el-aside width="200px"></el-aside>
                <el-main>
                    <el-container>
                        <el-aside width="200px"></el-aside>
                        <el-form ref="form" v-bind:model="user" label-width="100px"  :rules="rules">
                            <el-form-item label="电话|邮箱" prop="id" size="medium" >
                                <el-input v-model="user.id" placeholder="电话或邮箱"></el-input>
                            </el-form-item>
                            <el-form-item label="昵称" prop="name" size="medium">
                                <el-input v-model="user.name" placeholder="昵称" ></el-input>
                            </el-form-item>
                            <el-form-item label="密码" prop="password" size="medium">
                                <el-input v-model="user.password" placeholder="密码" type="password" show-password  ></el-input>
                            </el-form-item><br>
                            <el-form-item>
                                <el-button type="primary" @click="onSubmit">登录</el-button>
                                <el-button @click="resetForm('form')">重置</el-button><br><br>
                                <el-button type="text" style="color: red"><router-link to="register">没有账号点击这里哦！</router-link></el-button>
                            </el-form-item>
                        </el-form>


                    </el-container>

                </el-main>
            </el-container>
        </el-container>

    </div>
    <!-- v-on:click="onSubmit($event)" -->
</template>
<script>
    export default{
        name:'login',
        data(){
            return{
                user:{
                    id:'',
                    name:'',
                    password:''
                },
                rules: {
                    id: [
                        { required: true, message: '请输入电话', trigger: 'blur' },
                        { min: 10, max: 20, message: '请输入正确的电话或邮箱', trigger: 'blur' }
                    ],
                    name: [
                        { required: true, message: '请输入昵称', trigger: 'blur' },
                    ],
                    password: [
                        { required: true, message: '请输入密码', trigger: 'blur' },
                        { min: 8, max: 20, message: '密码长度不能小于8位大于20位', trigger: 'blur' }
                    ],
                }
            }
        },
        methods:{
            // Submit: function(event){
            //     event.preventDefault();
            //     let formData = JSON.stringify(this.equip);
            //     this.$axios.post('http://localhost:8081/Equip/login',formData).then(resp => {
            //     // success callback
            //     console.log(resp.header);
            //     return true;
            //     }).catch(err => {
            //     // error callback
            //     console.log(err);
            //     return false;
            // });
            // }
            // },
            // onSubmit(event) {
            // event.preventDefault();
            // let formData = JSON.stringify(this.equip);
            // console.log(formData);
            // this.$axios.post('http://localhost:8081/Equip/login',formData).then(function (resp) {
            //     // if (res.status === 200) {
            //     //     console.log("登录成功")
            //     // } else {
            //     //     console.log("登录失败")
            //     // }
            //     console.log(resp)
            // })
            // },
            onSubmit() {
                let _this = this
                if (this.user.id === ''){
                    return;
                }
                if (this.$cookie.get("id") !== null){
                    return;
                }

                // event.preventDefault();
                //qs.stringify()将对象 序列化成URL的形式，以&进行拼接  qs.parse()将URL解析成对象的形式
                // axios使用后会改变this  ，所以要事先保存this
                this.$axios.post('http://47.95.237.152:8081/User/login/', this.$qs.stringify({
                    // 这里的this。user。id是输入框也是data（）里的
                    id:this.user.id,
                    name:this.user.name,
                    password:this.user.password
                })).then(function(resp){
                    if(resp.status === 200){
                        let data = eval(resp.data)
                        if(data.code === 200){
                            _this.$stores.state.token = _this.user.id
                            _this.$router.push({
                                name:'home' ,  // 是index。js里路由的名字
                                // query 类似于get
                                params:{ //类似于post
                                    // 这里的_this。user。id是输入框也是data（）里的
                                    id:_this.user.id,
                                }
                            })
                            // eslint-disable-next-line no-undef
                            //设置一个cookie，让全局使用
                            _this.$cookie.set("id",_this.user.id);

                            // eslint-disable-next-line no-undef
                            // console.log(_this.$cookie.get("id"))
                            // alert(_this.$cookie.get("id"));
                        }else{
                            alert("用户密码或账户不对！")
                        }
                    }else{
                        _this.$router.push({
                            name:'error',
                        })
                    }
                })
            },
            setCookie(){

            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

