<script>

    export default {
        name: "Uequip",
        data (){
            return{
                datas: this.$route.params.data.data,
                totalPage: [], //所有页面数据
                pageSize: 20, //每页显示数量
                pageNum: 1,  //共几页，默认1页
                dataShow: '', //当前显示数据
                currentPage:0, //默认显示第一页
            }
        },
        methods:{
            check(){

            },
            HeadPage(){
                this.dataShow = this.totalPage[0];
            },
            nextPage(){
                //如果是最大长度了，则不能再向下了，所以return
                if(this.currentPage === this.pageNum - 1 ) return;
                this.dataShow = this.totalPage[++this.currentPage];
            },
            prePage(){
                //已经是最小了，所以return
                if (this.currentPage === 0) return;
                this.dataShow = this.totalPage[--this.currentPage];
            },
            removeCookie(){
                if(this.$cookie.get("id")){
                    this.$cookie.remove("id");
                    alert("注销成功！")
                }
            },
        },
        mounted() {
            let equips = this.datas
            this.pageNum = Math.ceil(equips.length / this.pageSize) || 1;
            // Math.ceil(equips.length /  this.pageSize) || 1;
            for (let i = 0; i < this.pageNum; i++) {
                //slice() 方法可从已有的数组中返回选定的元素。
                // slice()方法可提取字符串的某个部分，并以新的字符串返回被提取的部分。
                this.totalPage[i] = equips.slice(this.pageSize * i , this.pageSize *(i+1)) ;
            }
            console.log(this.pageNum)
            console.log(this.totalPage)
            this.dataShow = this.totalPage[this.currentPage];
        }
    }
</script>

