<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'

export default {
  name: 'Home',
  components: {
    // HelloWorld
  },
    data(){
        return{
            // datas:[],
            totalPage: [], //所有页面数据
            pageSize: 10, //每页显示数量
            pageNum: 1,  //共几页，默认1页
            dataShow: '', //当前显示数据
            currentPage:0, //默认显示第一页
            id:this.$cookie.get("id"),
            // name:this.$route.params.name,
            Qequip: {
                id:'',
                name:'',
            },
            Data:null,
        }
    },
    methods:{
        removeCookie(){
            if(this.$cookie.get("id")){
                this.$cookie.remove("id");
                alert("注销成功！")
                return;
            }
            alert("你还没有登录呢！")
        },
        HeadPage(){
            this.currentPage = 0;
            this.dataShow = this.totalPage[0];
        },
        nextPage(){
                //如果是最大长度了，则不能再向下了，所以return
            if(this.currentPage === this.pageNum - 1 ) return;
            this.dataShow = this.totalPage[++this.currentPage];
        },
        prePage(){
                //已经是最小了，所以return
            if (this.currentPage === 0) return;
            this.dataShow = this.totalPage[--this.currentPage];
        },
        check(row){
            let _this = this;
            if (this.id == null) {
                alert("亲！你还没有登录！不能添加装备哦！")
                return;
            }
            this.$axios.post("http://47.95.237.152:8081/UserEquip/addEquip",this.$qs.stringify({
                    eId: row.id,
                    uId: _this.id,
                })
            )
            alert("添加成功！")
        },
        queryEquip(){
            if (this.id == null) {
                alert("亲！你还没有登录！不能添加装备哦！")
                return
            }
            if (this.$store.state.userData == null){
                let _this = this
                this.$axios.post("http://47.95.237.152:8081/UserEquip/queryEquip",this.$qs.stringify({
                    uId:_this.id
                })).then(function (response) {
                    if (response.status === 200){
                        let data = response.data
                        // console.log(data)
                        // let evalData = eval(response.data)
                        // console.log(evalData)
                        if (data.code === 200){
                            _this.$store.state.userData = response.data;
                            // _this.$router.push({
                            //     name:'Uequip',
                            //     params:{
                            //         data:data
                            //     }
                            // })
                            _this.pushUserData(_this.$store.state.userData)
                        }
                    }
                })
            }else {
                this.pushUserData(this.$store.state.userData)
            }
        },
        Paging(data){
            let equips =  data;
            //Math.ceil() 函数返回大于或等于一个给定数字的最小整数。
            this.pageNum = Math.ceil(equips.length /  this.pageSize) || 1;
            for (let i = 0; i < this.pageNum; i++) {
                //slice() 方法可从已有的数组中返回选定的元素。
                // slice()方法可提取字符串的某个部分，并以新的字符串返回被提取的部分。
                this.totalPage[i] =  equips.slice(this.pageSize * i,this.pageSize * (i+1));
            }
            this.dataShow = this.totalPage[this.currentPage];
        },
        pushUserData(data){
            this.$router.push({
                name:'Uequip',
                params:{data:data}
            })
        },
        queryAll() {
            this.Paging(this.$store.state.data)
        },
        queryHandle(i){
            switch (i) {
                case "魔法": return this.$store.state.AllData.MagicData;
                case "咒术": return this.$store.state.AllData.ConjuringData;
                case "武器": return this.$store.state.AllData.EquipData;
                case "防具": return this.$store.state.AllData.ArmorData;
                case "弓弩": return this.$store.state.AllData.CrossbowData;
                case "戒指": return this.$store.state.AllData.RingData;
                case "道具": return this.$store.state.AllData.PropsData;
                case "特殊物品": return this.$store.state.AllData.SpecialArticlesData;
                case "灵魂": return this.$store.state.AllData.SoulData;
                case "NPC": return this.$store.state.AllData.NPCData;
            }
        },
        query(str){  //魔法
            // if(this.$store.state.MagicData.length === 0) {
            //     let equips =  this.$store.state.data
            //     let j = 0
            //     for (let i = 0; i < equips.length; i++) {
            //         if (equips[i].kind === "魔法"){
            //             this.$store.state.MagicData[j++] = equips[i]
            //         }
            //     }
            // }
            // this.Paging(this.$store.state.MagicData)
            let Data = this.queryHandle(str)
            if (Data.length === 0){
                let equips =  this.$store.state.data
                let j = 0
                for (let i = 0; i < equips.length; i++) {
                    if (equips[i].kind === str){
                        Data[j++] = equips[i]
                    }
                }
            }
            this.Paging(Data)
        },
        queryQequip(){
            if (this.Qequip.id === '' && this.Qequip.name === ''){
                return
            }
            let data = this.$store.state.data
            let ID = this.Qequip.id-1
            if (data[ID] !== ''){
                this.$store.state.TempData = data[ID]
                console.log(this.$store.state.TempData)
            }


        }
        // queryConjuring(data){  //咒术
        //
        //
        // },
        // eslint-disable-next-line no-unused-vars
       // :row-class-name="tableRowClassName"
       //  tableRowClassName({row, rowIndex}) {
       //      return 'success-row';
       //  }
    },
    created(){
        let _this = this
      if (this.$store.state.data == null){

          this.$axios.get('http://47.95.237.152:8081/Equip/get')
              // .then(resp =>(this.datas = resp.data.data))
              // .then(function(resp){
              // 用eval将json转化为字符串
              //     let data = eval(resp.data.data)
              //     for (let i = 0; i < data.length; i++) {
              //         let equip = eval(data[i])
              //         this.datas = equip
              //     }
              // })
              .then( function (response) {
                  // let equips =  response.data.data;
                  _this.$store.state.data = response.data.data;
                  //Math.ceil() 函数返回大于或等于一个给定数字的最小整数。
                  // _this.pageNum = Math.ceil(equips.length /  _this.pageSize) || 1;
                  // for (let i = 0; i < _this.pageNum; i++) {
                  //     //slice() 方法可从已有的数组中返回选定的元素。
                  //     // slice()方法可提取字符串的某个部分，并以新的字符串返回被提取的部分。
                  //     _this.totalPage[i] =  equips.slice(_this.pageSize * i,_this.pageSize * (i+1));
                  // }
                  //
                  // console.log(_this.totalPage)
                  // _this.dataShow = _this.totalPage[_this.currentPage];
                  _this.Paging(_this.$store.state.data)
              })
              .catch(function (error) { // 请求失败处理
                  console.log(error);
              });
      }else {
          this.Paging(this.$store.state.data)
      }


    },
}
</script>

