<template>
    <div class="home">
        <el-container>
            <el-header style="font-size: 14px; " >
                <div style="text-align: left">
                    <el-row :gutter="20">
                        <el-col :span="12">
                            <div class="grid-content bg-purple">
                            <el-col :span="3">
                                <div class="grid-content bg-purple">
                                    <router-link to="/" style="color: azure"><el-button type="primary"  >主页</el-button></router-link>
                                </div>
                            </el-col>
                            <el-col :span="5">
                                    <div class="grid-content bg-purple">
                                        <el-button type="primary" @click="queryEquip" >查看已有装备</el-button>
                                    </div>
                            </el-col>
                            <el-col :span="5">
                                    <div class="grid-content bg-purple">
                                        <el-button type="primary"  >成为vip</el-button>
                                    </div>
                                </el-col>

                            </div>
                        </el-col>
                        <el-col :span="3" :offset="9">
                            <div class="grid-content bg-purple">
                                <span> 欢迎{{user.id}} 登录 </span>
                            </div>
                        </el-col>
                    </el-row>
                </div>
            </el-header>
            <el-container>
                <el-aside width="200px"></el-aside>
                <el-main>
                    <el-container>
                        <el-aside width="200px"></el-aside>
                    </el-container>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>
<script>
    export default{
        name:'home',
        data(){
            return{
                user:{
                    id:this.$cookie.get("id"),
                }
            }
        },
        methods:{
            queryEquip(){
                let _this = this

                this.$axios.post("http://47.95.237.152:8081/UserEquip/queryEquip",this.$qs.stringify({
                    uId:_this.user.id
                })).then(function (response) {
                    if (response.status === 200){
                        let data = response.data
                        // console.log(data)
                        // let evalData = eval(response.data)
                        // console.log(evalData)

                        if (data.code === 200){
                            _this.$router.push({
                                name:'Uequip',
                                params:{
                                    data:data
                                }
                            })
                        }
                    }

                })
            },

        }
    }
</script>